import { Image } from "antd";
import moment from "moment";
import React from "react";
import styled from "styled-components";
import { decryptText } from "../../utils/crypt";

const MessageContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  align-items: ${({ isUser }) => (isUser ? "flex-end" : "flex-start")};
`;

export const MessageBubble = styled.div`
  max-width: 60%;
  padding: 10px;
  border-radius: 10px;
  background-color: ${({ isUser }) => (isUser ? "#525cb1" : "#F6F6F6")};
  color: ${({ isUser }) => (isUser ? "white" : "#3f3c3c")};
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  /* white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; */
`;

const MessageInfo = styled.div`
  font-size: 10px;
  margin-top: 5px;
  color: #666;
  font-style: italic;
`;

const Message = ({ text, sender, time, image, type, receiverId }) => {
  const userId = localStorage.getItem("userId");
  const isUser = sender === userId ? true : false;
  const isMissedImage = !image && type === "image";
  return (
    <MessageContainer isUser={isUser}>
      {type !== "image" && (
        <MessageBubble isUser={isUser}>
          {decryptText(
            text,
            isUser ? receiverId : localStorage.getItem("userId")
          )}
          {/* {text} */}
        </MessageBubble>
      )}

      {image && <Image width={300} src={image} />}

      {isMissedImage && (
        <Image
          width={80}
          src="error"
          fallback="data:image/png;base64,"
        />
      )}

      {isMissedImage && (
        <small style={{ color: "gray" }}>
          <i>Image was sent to you when you are not on this widnow</i>
        </small>
      )}

      <MessageInfo>{moment(time).format("Do MMM YY, h.mm a")}</MessageInfo>
    </MessageContainer>
  );
};

export default Message;
